import React, { useState, useEffect } from 'react'
import { useHistory } from 'react-router-dom'
import { Row, Col, Typography, Button, Card, Statistic, Table, Form, Spin, Tooltip } from 'antd'
import * as anticons from '@ant-design/icons'

import { getLabel, labels } from '../../utils/Internationalization'

import UsuarioService from '../../service/UsuarioService'
import toast from 'react-hot-toast'
import AlertWait from '../../pages/AlertWait'

const usuarioService = new UsuarioService()

const tableProps = {
    bordered: false,
    loading: false,
    title: '',
    showHeader: true,
    size: 'medium',
    pagination: {
        position: ['none', 'bottomRight']
    }
}

const estilos = [
    { background: '#54873C' },
    { background: '#70B450' },
    { background: '#385A28' },
    { background: '#636466' },
    { background: '#70B450' },
    { background: '#54873C' },
    { background: '#385A28' },
]

const Dashboard = () => {

    const navigate = useHistory()
    const [load, setLoad] = useState(true)
    const [loadTotals, setLoadTotals] = useState(true)
    const [isLoading, setIsLoading] = useState(false)
    const [users, setUsers] = useState([])
    const [isLoadingTotals, setIsLoadingTotals] = useState(false)
    const [totals, setTotals] = useState(null)
    const [total, setTotal] = useState(0)

    // const statistics = [
    //     {
    //         id: 1,
    //         props: {
    //             bordered: false,
    //             hoverable: true,
    //             style: {
    //                 background: '#54873C',
    //                 color: '#f0f0f0'
    //             }
    //         },
    //         data: {
    //             title: <Typography.Text style={{ color: '#ffffff' }}>{getLabel(labels.trainerOfTrainers)}</Typography.Text>,
    //             value: 10,
    //             precision: 2,
    //             prefix: <anticons.UserOutlined />,
    //             suffix: '%'
    //         },
    //         description: <>120 {getLabel(labels.users)}</>
    //     },
    //     {
    //         id: 2,
    //         props: {
    //             bordered: false,
    //             hoverable: true,
    //             style: {
    //                 background: '#70B450',
    //                 color: '#f0f0f0'
    //             }
    //         },
    //         data: {
    //             title: <Typography.Text style={{ color: '#ffffff' }}>{getLabel(labels.trainer)}</Typography.Text>,
    //             value: 10,
    //             precision: 2,
    //             prefix: <anticons.UserOutlined />,
    //             suffix: '%'
    //         },
    //         description: <>120 {getLabel(labels.users)}</>
    //     },
    //     {
    //         id: 3,
    //         props: {
    //             bordered: false,
    //             hoverable: true,
    //             style: {
    //                 background: '#385A28',
    //                 color: '#f0f0f0'
    //             }
    //         },
    //         data: {
    //             title: <Typography.Text style={{ color: '#ffffff' }}>{getLabel(labels.applicants)}</Typography.Text>,
    //             value: 80,
    //             precision: 2,
    //             prefix: <anticons.UserOutlined />,
    //             suffix: '%'
    //         },
    //         description: <>120 {getLabel(labels.users)}</>
    //     },
    //     {
    //         id: 4,
    //         props: {
    //             bordered: false,
    //             hoverable: true,
    //             style: {
    //                 background: '#636466',
    //                 color: '#f0f0f0'
    //             }
    //         },
    //         data: {
    //             title: <Typography.Text style={{ color: '#ffffff' }}>{getLabel(labels.beneficiariesTitle)}</Typography.Text>,
    //             value: 150,
    //             precision: 0,
    //             prefix: <anticons.UserOutlined />,
    //             suffix: ''
    //         },
    //         description: <>&nbsp;</>
    //     }
    // ]

    const columnas = [
        {
            title: '',
            render: (_, r) => (r.imagen && <img alt='' src={`/images/${r.imagen.split('/')[r.imagen.split('/').length - 1]}`} width={32} height={32} />) || <img alt='' src='./assets/user.png' width={32} height={32} />
        },
        {
            title: getLabel(labels.firstName),
            dataIndex: 'nombres',
            render: (_, r) => `${r.nombres}`
        },
        {
            title: getLabel(labels.lastName),
            dataIndex: 'apellidos',
            render: (_, r) => `${r.apellidos}`
        },
        {
            title: getLabel(labels.country),
            dataIndex: 'pais',
            render: (_, { pais }) => <div style={{ textAlign: 'center' }}>
                <Tooltip title={pais.catDescripcion}>
                    <img
                        src={`https://flagcdn.com/32x24/${pais.catDescripcion.toLowerCase()}.png`}
                        srcSet={`https://flagcdn.com/32x24/${pais.catDescripcion.toLowerCase()}.png 2x,https://flagcdn.com/96x72/${pais.catDescripcion.toLowerCase()}.png 3x`}
                        width="32"
                        height="24"
                        alt={pais.catDescripcion}></img>
                </Tooltip>
            </div>,
            responsive: ['md', 'lg', 'xl', 'xxl']
        },
        {
            title: <div style={{ textAlign: 'center' }}>
                {getLabel(labels.rol)}
            </div>,
            dataIndex: 'role',
            render: (_, { perfiles }) => <div style={{ textAlign: 'center' }}>
                {perfiles[0].perNombre}
            </div>,
            responsive: ['md', 'lg', 'xl', 'xxl']
        },
        {
            title: getLabel(labels.actions),
            render: (_, record) => <Button type='primary' href={`/#/user/${record.usuId}`}>
                {getLabel(labels.view)}
            </Button>
        }
    ]

    // const renderStatistics = ({ id, props, data, description }) =>
    //     <Col
    //         key={id}
    //         xs={12} sm={6} md={6} lg={6} xl={6} xxl={6}
    //     >
    //         <Card
    //             {...props}
    //         >
    //             <Statistic
    //                 {...data}
    //                 valueStyle={{
    //                     color: '#ffffff',
    //                 }}
    //             />
    //             {description}
    //         </Card>
    //     </Col>

    const renderTotal = ({ id, props, data, description }) =>
        <Col
            key={id}
            xs={12} sm={6} md={6} lg={6} xl={6} xxl={6}
        >
            <Card
                {...props}
            >
                <Statistic
                    {...data}
                    valueStyle={{
                        color: '#ffffff',
                    }}
                />
                {description}
            </Card>
        </Col>

    const fetchDataTotals = async () => {
        setLoadTotals(false)
        setIsLoadingTotals(true)
        try {
            const { data } = await usuarioService.getTotalUsersByProfile({ navigate })
            if (data) {
                const totales = data.listObject
                const initialValue = 0;
                const sumWithInitial = totales.reduce(
                    (accumulator, currentValue) => accumulator + currentValue.total,
                    initialValue,
                )
                setTotal(sumWithInitial)
                setTotals(totales)
            } else {
                toast.error(getLabel(labels.errorOcurred))
            }
        } catch (error) {
            const { response } = error
            toast.error(response.status === 409 ? response.data[0].messageText : getLabel(labels.errorOcurred))
        } finally {
            setIsLoadingTotals(false)
        }
    }

    const fetchData = async () => {
        setLoad(false)
        setIsLoading(true)
        try {
            const { data } = await usuarioService.getLessPostulants({ navigate })
            if (data) {
                const usuarios = data.listObject
                setUsers(usuarios)
            } else {
                toast.error(getLabel(labels.errorOcurred))
            }
        } catch (error) {
            const { response } = error
            toast.error(response.status === 409 ? response.data[0].messageText : getLabel(labels.errorOcurred))
        } finally {
            setIsLoading(false)
        }
    }

    useEffect(() => {
        if (load) fetchData()
        if (loadTotals) fetchDataTotals()
    })

    return (
        <>
            {
                isLoadingTotals ?
                    <AlertWait />
                    :
                    totals && <Row gutter={[15, 15]}>
                        {
                            totals && totals.map((t, index) => (
                                {
                                    id: t.perId,
                                    props: {
                                        bordered: false,
                                        hoverable: false,
                                        style: {
                                            color: '#f0f0f0'
                                            , ...estilos[index]
                                        }
                                    },
                                    data: {
                                        title: <Typography.Text style={{ color: '#ffffff' }}>{t.perNombre}</Typography.Text>,
                                        value: Math.round(t.total * 100.00 / total, 1),
                                        precision: 0,
                                        prefix: <anticons.UserOutlined />,
                                        suffix: '%'
                                    },
                                    description: <>{t.total} {getLabel(labels.of)} {total} {getLabel(labels.users)}</>
                                }
                            )).map(s => renderTotal(s))
                        }
                    </Row>
            }
            <Row justify='end'>
                <Col style={{ padding: '15px 0' }}>
                    <Form
                        layout='inline'
                    >
                        <Form.Item>
                            <Button type='default'
                                onClick={() => fetchData()}
                            >
                                {getLabel(labels.refresh)}
                            </Button>
                        </Form.Item>
                    </Form>
                </Col>
            </Row>
            {
                isLoading ?
                    <Row style={{ padding: '30px 10px' }}>
                        <Col span={24} style={{ textAlign: 'center' }}>
                            <Spin size='large' tip={
                                <Typography.Paragraph style={{ padding: '10px 0' }}>
                                    {getLabel(labels.loading)}...
                                </Typography.Paragraph>
                            } />
                        </Col>
                    </Row>
                    :
                    <Row>
                        <Col span={24}>
                            <Table
                                {...tableProps}
                                rowKey='usuId'
                                columns={columnas}
                                dataSource={users}>
                            </Table>
                        </Col>
                    </Row>
            }
        </>
    )
}

export default Dashboard